.main {
  display: flex;
  justify-content: center;
  height: calc(100% - 112px - 111px);
  margin: 112px auto;
  padding: 0 32px;
}

.centerBlock {
  display: flex;
  position: fixed;
  /* left: 50%;
  transform: translateX(-50%); */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  width: 100%;
  height: calc(100% - 112px - 111px);
  margin: 112px auto;
  padding: 0 32px;
}

.problem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 768px;
  height: 108px;
  border-radius: 24px;
  background: #f3f0e9;
  cursor: pointer;
}
